  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  overflow-y: auto;
  position: fixed;
  z-index: 6;
  left: -320px; }
  .slide-menu--shadow--on {
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
  .slide-menu--shadow--off {
