  .slide-menu__link:last-child {
    margin-bottom: 0; }
  .slide-menu__link--active {
    color: #7e7e82; }
  .slide-menu__link--active::before {
    content: '';
    position: relative;
    left: -24px;
    width: 4px;
    height: 32px;
    background-color: #41f6d7;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px; }
  .slide-menu__bottom {
